import { reactive, toRefs, onBeforeMount } from 'vue';
import { Store, useStore } from 'vuex';
import { ESORT, ECOUNTER, ELETTER, EDURATION, ISelectModule, ISelectOption, IStoreState } from '@/types';
import BH from '@/store/behaviors';


interface IPullMenuData {
    display: ISelectModule;
    character: ISelectModule;
    category: ISelectModule;
    duration: ISelectModule;
}
interface IInitItem {
    list: Array<ISelectOption>;
    compare: ESORT | ECOUNTER | ELETTER | EDURATION;
}


export default function() {

    const store: Store<IStoreState> = useStore();

    const data: IPullMenuData = reactive({
        display: {
            name: 'sort',
            show: false,
            list: [
                {
                    id: 1,
                    text: '顺序显示',
                    selected: true,
                    value: ESORT.ORDER
                },
                {
                    id: 2,
                    text: '随机显示',
                    selected: false,
                    value: ESORT.RANDOM
                }
            ]
        },
        character: {
            name: 'counter',
            show: false,
            list: [
                {
                    id: 1,
                    text: '100个字符',
                    selected: true,
                    value: ECOUNTER.HUNDRED
                },
                {
                    id: 2,
                    text: '80个字符',
                    selected: false,
                    value: ECOUNTER.EIGHTY
                },
                {
                    id: 3,
                    text: '60个字符',
                    selected: false,
                    value: ECOUNTER.SIXTY
                },
                {
                    id: 4,
                    text: '50个字符',
                    selected: false,
                    value: ECOUNTER.FIFTY
                },
                {
                    id: 5,
                    text: '40个字符',
                    selected: false,
                    value: ECOUNTER.FORTY
                },
                {
                    id: 6,
                    text: '30个字符',
                    selected: false,
                    value: ECOUNTER.THIRTY
                },
                {
                    id: 7,
                    text: '20个字符',
                    selected: false,
                    value: ECOUNTER.TWENTY
                },
                {
                    id: 8,
                    text: '10个字符',
                    selected: false,
                    value: ECOUNTER.TEN
                }
            ]
        },
        category: {
            name: 'letter',
            show: false,
            list: [
                {
                    id: 1,
                    text: '全部',
                    selected: true,
                    value: ELETTER.ALL
                },
                {
                    id: 2,
                    text: '只显示英文小写',
                    selected: false,
                    value: ELETTER.LOWERCASE
                },
                {
                    id: 3,
                    text: '只显示英文大写',
                    selected: false,
                    value: ELETTER.UPPERCASE
                },
                {
                    id: 4,
                    text: '只显示英文大小写',
                    selected: false,
                    value: ELETTER.LOW_UP_CASE
                },
                {
                    id: 5,
                    text: '只显示数字',
                    selected: false,
                    value: ELETTER.NUMBER
                },
                {
                    id: 6,
                    text: '只显示符号',
                    selected: false,
                    value: ELETTER.SYMBOL
                }
            ]
        },
        duration: {
            name: 'duration',
            show: false,
            list: [
                {
                    id: 1,
                    text: '提示1秒',
                    selected: false,
                    value: EDURATION.ONE
                },
                {
                    id: 2,
                    text: '提示2秒',
                    selected: false,
                    value: EDURATION.TWO
                },
                {
                    id: 3,
                    text: '提示3秒',
                    selected: false,
                    value: EDURATION.THREE
                },
                {
                    id: 4,
                    text: '提示4秒',
                    selected: false,
                    value: EDURATION.FOUR
                },
                {
                    id: 5,
                    text: '提示5秒',
                    selected: false,
                    value: EDURATION.FIVE
                },
                {
                    id: 6,
                    text: '一直提示',
                    selected: true,
                    value: EDURATION.HOLD
                }
            ]
        }
    })

    function onEventHandler(e: { name: string, id?: number }): void {
        let array: Array<ISelectModule> = [ data.display, data.character, data.category, data.duration ];
        for (let i = 0, length = array.length; i < length; i++) {
            if (array[i].name === e.name) {
                if (e.id === undefined) {
                    array[i].show = !array[i].show;
                } else {
                    array[i].list.forEach((o: ISelectOption) => {
                        if (o.id === e.id) {
                            o.selected = true;
                            switch(e.name) {
                                case data.display.name:
                                    store.dispatch(BH.SET_SORT, o.value);
                                    break;
                                case data.character.name: 
                                    store.dispatch(BH.SET_COUNTER, o.value);
                                    break;
                                case data.category.name:
                                    store.dispatch(BH.SET_LETTER, o.value);
                                    break;
                                case data.duration.name:
                                    store.dispatch(BH.SET_DURATION, o.value);
                                    break;
                            }
                        } else {
                            o.selected = false;
                        }
                    });
                    array[i].show = false;
                }
            } else {
                array[i].show = false;
            }
        }
    }

    function onOutsideClick(): void {
        let array: Array<ISelectModule> = [ data.display, data.character, data.category, data.duration ];
        array.forEach(m => { m.show = false; });
    }

    function _onInitialization(): void {
        let array: Array<IInitItem> = [
            {
                list: data.display.list,
                compare: store.state.sort
            }, {
                list: data.character.list,
                compare: store.state.counter
            }, {
                list: data.category.list,
                compare: store.state.letter
            }, {
                list: data.duration.list,
                compare: store.state.duration
            }
        ];
        for (let i = 0, length = array.length; i < length; i++) {
            for (let k = 0, len = array[i].list.length; k < len; k++) {
                if (array[i].list[k].value === array[i].compare) {
                    array[i].list[k].selected = true;
                } else {
                    array[i].list[k].selected = false;
                }
            }
        }
    }

    onBeforeMount(() => {
        _onInitialization();
    })

    return {
        ...toRefs(data),
        onEventHandler,
        onOutsideClick
    }
}