
    import { defineComponent, PropType, inject, ComputedRef, computed } from 'vue';
    import { ISelectOption, ISelectModule } from '@/types';

    export default defineComponent({
        props: {
            options: {
                type: Object as PropType<ISelectModule>,
                default() {
                    return {}
                },
                require: true
            }
        },
        setup(props: { options: ISelectModule }, { emit }) {

            let _onRestart: Function = inject('restart') as Function;

            const show_text: ComputedRef<string> = computed(() => {
                return props.options.list.find((v: ISelectOption) => v.selected)?.text || '';
            });

            function onShowTextClick(): void {
                emit('eventHandler', {
                    name: props.options.name
                });
            }

            function onOptionClick(_id: number): void {
                emit('eventHandler', {
                    name: props.options.name,
                    id: _id
                });
                setTimeout(() => { _onRestart(); }, 500);
            }

            return {
                show_text,
                onShowTextClick,
                onOptionClick
            }
        }
    })
