import { Ref, ref, computed, ComputedRef } from 'vue';
import { Store, useStore } from 'vuex';
import { IStoreState } from '@/types';
import BH from '@/store/behaviors';
import KS from '@/hooks/keyshelf';
import zeroize from '@/assets/scripts/zeroize';


export function useBottomArea() {
    const store: Store<IStoreState> = useStore();

    const times: ComputedRef<string> = computed(() => {
        let _h: number = store.state.stage >= 3600000 ? Math.floor(store.state.stage / 3600000) : 0; 
        let _m: number = store.state.stage >= 60000 ? Math.floor((store.state.stage - _h * 3600000) / 60000) : 0;
        let _s: number = Math.floor((store.state.stage - _h * 3600000 - _m * 60000) / 1000);
        return `${ _h > 0 ? _h.toString().padStart(2, '0').concat('时') : '' }${ _m.toString().padStart(2, '0') }分${ _s.toString().padStart(2, '0') }秒`;
    });
    const speed: ComputedRef<string> = computed(() => {
        if (store.state.stage === 0) return '--';
        return Math.floor(store.state.accomplish * 60000 / store.state.stage).toString();
    });
    const accuracy: ComputedRef<string> = computed(() => {
        if (store.state.total === 0) return '--';
        let mod: number = store.state.correct * 100 % store.state.total;
        let acc: string = (store.state.correct * 100 / store.state.total).toString();
        return mod === 0 ? acc : zeroize(acc);
    });
    const progress: ComputedRef<string> = computed(() => {
        let mod: number = store.state.accomplish * 100 % store.state.counter;
        let pro: string = (store.state.accomplish * 100 / store.state.counter).toString();
        return mod === 0 ? pro : zeroize(pro);
    });

    const remind: Ref<boolean> = ref(store.state.prompt);

    function onRemindClick(): void {
        remind.value = !remind.value;
        store.dispatch(BH.SET_PROMPT, remind.value);
        KS.onSetKeyfieldGuidance();
    }

    return {
        times,
        speed,
        accuracy,
        progress,
        remind,
        onRemindClick
    }
}