import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import popup from './libs/popup/global_popup';
import clickout from './libs/outside';

createApp(App)
    .use(store)
    .use(router)
    .use(popup)
    .use(clickout)
    .mount('#app');
