<template>
    <div class="select-box-page">
        <div class="show-layer" @click="onShowTextClick">{{ show_text }}</div>
        <transition name="motion">
            <div class="option-box" v-if="options.show">
                <div class="option-text"
                    v-for="item in options.list"
                    :key="'S' + item.id"
                    :class="{ selected: item.selected }"
                    @click="onOptionClick(item.id)"
                >{{ item.text }}</div>
            </div>
        </transition>
    </div>
</template>

<script lang="ts">
    import { defineComponent, PropType, inject, ComputedRef, computed } from 'vue';
    import { ISelectOption, ISelectModule } from '@/types';

    export default defineComponent({
        props: {
            options: {
                type: Object as PropType<ISelectModule>,
                default() {
                    return {}
                },
                require: true
            }
        },
        setup(props: { options: ISelectModule }, { emit }) {

            let _onRestart: Function = inject('restart') as Function;

            const show_text: ComputedRef<string> = computed(() => {
                return props.options.list.find((v: ISelectOption) => v.selected)?.text || '';
            });

            function onShowTextClick(): void {
                emit('eventHandler', {
                    name: props.options.name
                });
            }

            function onOptionClick(_id: number): void {
                emit('eventHandler', {
                    name: props.options.name,
                    id: _id
                });
                setTimeout(() => { _onRestart(); }, 500);
            }

            return {
                show_text,
                onShowTextClick,
                onOptionClick
            }
        }
    })
</script>

<style lang="scss" scoped>
    .select-box-page {
        height: 0.25rem;

        .show-layer {
            position: relative;
            z-index: 10;
            height: 0.25rem;
            background-color: #444;
            font-size: 0.1rem;
            color: #999;
            line-height: 0.25rem;
            text-align: center;
            cursor: pointer;
        }
        .option-box {
            background-color: #444;
            transform: translateY(-100%) translateY(-0.25rem);
            -webkit-transform: translateY(-100%) translateY(-0.25rem);
            -o-transform: translateY(-100%) translateY(-0.25rem);
            -ms-transform: translateY(-100%) translateY(-0.25rem);
            -moz-transform: translateY(-100%) translateY(-0.25rem);

            .option-text {
                height: 0.25rem;
                font-size: 0.1rem;
                color: #999;
                line-height: 0.25rem;
                text-align: center;
                padding: 0 0.05rem;
                cursor: pointer;

                &.selected {
                    background-color: #40c7db;
                    font-weight: bold;
                    color: #111;
                }
            }
        }
        .motion-enter-active, .motion-leave-active {
            transition: all 0.3s ease;
        }
        .motion-enter-from, .motion-leave-to {
            transform: translateY(-100%);
            -webkit-transform: translateY(-100%);
            -o-transform: translateY(-100%);
            -ms-transform: translateY(-100%);
            -moz-transform: translateY(-100%);
            opacity: 0;
        }
    }
</style>